/* Custom Animations */
.reveal-text {
  animation: reveal 0.8s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  opacity: 0;
  transform: translateY(20px);
}

@keyframes reveal {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.clip-path-reveal {
  clip-path: inset(100% 0 0 0);
  transition: clip-path 1.2s cubic-bezier(0.16, 1, 0.3, 1);
}

.in-view .clip-path-reveal {
  clip-path: inset(0 0 0 0);
}

.stagger-item {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.in-view .stagger-item:nth-child(1) {
  transition-delay: 0.2s;
  opacity: 1;
  transform: translateY(0);
}

.in-view .stagger-item:nth-child(2) {
  transition-delay: 0.4s;
  opacity: 1;
  transform: translateY(0);
}

.in-view .stagger-item:nth-child(3) {
  transition-delay: 0.6s;
  opacity: 1;
  transform: translateY(0);
}

.scale-image-reveal {
  transform: scale(1.15);
  opacity: 0;
  transition: transform 1.5s cubic-bezier(0.25, 1, 0.5, 1), opacity 1s ease-out;
}

.in-view .scale-image-reveal {
  transform: scale(1);
  opacity: 0.6;
}

@keyframes width {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

